*, :before, :after {
  box-sizing: border-box;
  text-decoration: none;
  list-style: none;
}

:root {
  --font-primary: "Poppins", sans-serif;
  --font-secondary: "Alice", sans-serif;
  --primary-color: #04b90b;
  --black: #000;
  --gray: #777e90;
  --black-2: #23262f;
  --bg-primary: #fcfcfd;
  --gray-2: #f4f5f6;
  --gray-3: #353945;
  --btn-color: #192a56;
}

* {
  font: inherit;
  margin: 0;
  padding: 0;
}

img, picture, svg, video {
  max-width: 100%;
  display: block;
}

input, select, textarea {
  background-color: #0000;
  outline: none;
}

button {
  cursor: pointer;
  background-color: #0000;
  border: 0;
  outline: none;
}

body {
  font-size: 16px;
  font-weight: 400;
  line-height: 1;
  font-family: var(--font-primary);
  background-color: var(--bg-primary);
  color: var(--black);
  min-height: 100vh;
}

.container {
  width: 100%;
  max-width: 1230px;
  margin: 0 auto;
  padding: 0 15px;
}

.header {
  z-index: 1000;
  background-color: #fff;
  padding: 25px 0;
  position: sticky;
  top: 0;
  left: 0;
  right: 0;
  box-shadow: 0 5px 15px #0000001a;
}

.header-main {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.header-logo {
  cursor: pointer;
}

.header-nav {
  align-items: center;
  gap: 30px;
  display: flex;
}

.header-link {
  color: var(--black);
  font-size: 18px;
  font-weight: 400;
  line-height: 30px;
}

.header-link:hover {
  color: var(--primary-color);
  transition: all .2s linear;
}

.header-link.active {
  color: var(--primary-color);
}

.header-icon {
  align-items: center;
  gap: 30px;
  font-size: 22px;
  display: flex;
}

.header__bar {
  display: none;
}

.header-icon a, .header-icon i {
  text-align: center;
  color: var(--btn-color);
  background-color: #eee;
  border-radius: 100rem;
  width: 45px;
  height: 45px;
  line-height: 45px;
  transition: all .2s linear;
}

.header-icon a:hover, .header-icon i:hover {
  color: #fff;
  background-color: var(--primary-color);
  transform: rotate(180deg);
}

.header-list__mobile {
  display: none;
}

.cart {
  width: 100%;
  padding: 80px 0;
}

table {
  border-collapse: collapse;
  caption-side: bottom;
  width: 100%;
}

table thead tr th {
  color: #fff;
  text-align: center;
  border-color: #e3a51e #e3a51e #0000;
  border-bottom-style: solid;
  border-bottom-width: 0;
  padding: 15px 20px;
  font-size: 14px;
  font-weight: 600;
}

table thead {
  background-color: #04b90b;
}

table tbody tr td {
  text-align: center;
  vertical-align: middle;
  color: #333;
  border: 0 solid #ccc;
  border-bottom-width: 2px;
  padding: 8px 4px;
}

th.pro-thumnail {
  width: 150px;
}

td.pro-title a {
  cursor: pointer;
  color: #333;
  text-transform: capitalize;
  width: 120px;
  font-size: 15px;
  font-weight: 500;
  transition: all .3s;
}

td.pro-title a:hover {
  color: #e3a51e;
}

td.pro-remove i {
  cursor: pointer;
  font-size: 19px;
  transition: all .3s;
}

td.pro-remove i:hover {
  color: #e3a51e;
}

td.pro-remove button {
  background-color: var(--primary-color);
  color: #fff;
  border-radius: 6px;
  justify-content: center;
  align-items: center;
  gap: 8px;
  width: 100%;
  padding: 15px 20px;
  display: flex;
}

.cart-quantity {
  margin-right: 0;
  padding: 0 15px;
}

.basketTotalContainer {
  justify-content: flex-end;
  width: 100%;
  padding: 10px 0;
  display: flex;
}

.basketTotal-title {
  width: 30%;
}

.hero {
  padding: 40px 0;
}

.hero-main {
  justify-content: space-between;
  gap: 40px;
  display: flex;
}

.hero-content {
  width: 100%;
  max-width: 700px;
}

.hero-title {
  font-size: 65px;
  font-weight: 400;
  font-family: var(--font-secondary);
  letter-spacing: -0.5%;
  margin-bottom: 30px;
  line-height: 70px;
}

.hero-desc {
  color: var(--gray);
  margin-bottom: 25px;
  font-size: 24px;
  font-weight: 400;
  line-height: 36px;
}

.hero-cta {
  margin-bottom: 105px;
}

.hero-btn {
  align-items: center;
  gap: 24px;
  display: flex;
}

.hero-btn span {
  color: var(--primary-color);
  font-size: 16px;
  font-weight: 400;
  line-height: 24px;
}

#role {
  padding: 8px;
}

.button-primary {
  background-color: var(--btn-color);
  color: var(--bg-primary);
  border-radius: 8px;
  padding: 10px 30px;
  font-size: 16px;
  font-weight: 500;
  line-height: 24px;
  transition: all .2s linear;
}

.button-primary:hover {
  background-color: var(--primary-color);
  letter-spacing: 1px;
}

.hero-info {
  background-color: var(--gray-2);
  border-radius: 16px;
  width: 100%;
  max-width: 576px;
  padding: 28px 56px;
}

.hero-list {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.hero-item {
  text-align: center;
}

.hero-number {
  color: var(--gray);
  font-size: 32px;
  font-weight: 600;
  line-height: 48px;
}

.hero-text {
  color: var(--gray);
  font-size: 20px;
  font-weight: 400;
  line-height: 30px;
}

.hero-image, .hero-image img {
  width: 100%;
}

.text-center__primary {
  text-align: center;
  font-size: 48px;
  font-weight: 400;
  line-height: 55px;
  font-family: var(--font-secondary);
  text-transform: capitalize;
  max-width: 440px;
  margin: 0 auto;
}

.text-primary {
  color: var(--primary-color);
}

.service-list {
  grid-gap: 70px;
  grid-template-columns: repeat(3, 1fr);
  margin-top: 40px;
  display: grid;
}

.service-item {
  text-align: center;
  cursor: pointer;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 40px 36px;
  display: flex;
}

.service-item:hover {
  background: #fff;
  transition: all .2s linear;
  box-shadow: 0 10px 27px #3535350d;
}

.service-icon {
  margin-bottom: 24px;
}

.service-icon a {
  background: #fff;
  border-radius: 100rem;
  justify-content: center;
  align-items: center;
  width: 120px;
  height: 120px;
  display: inline-flex;
  box-shadow: 5px 9px 16px #0000000a;
}

.service-title {
  color: var(--black-2);
  margin-bottom: 6px;
  font-size: 28px;
  font-weight: 400;
  line-height: 42px;
}

.service-desc {
  color: var(--gray-3);
  font-size: 16px;
  font-weight: 400;
  line-height: 24px;
}

.about {
  padding: 40px 0;
}

.about-heading {
  text-align: center;
}

.about-main {
  justify-content: space-between;
  align-items: center;
  gap: 130px;
  margin-top: 40px;
  display: flex;
}

.about-center {
  max-width: 475px;
}

.about-image {
  width: 100%;
  max-width: 500px;
}

.about-image img, .about-content {
  width: 100%;
}

.about-info {
  font-family: var(--font-secondary);
  align-items: center;
  gap: 94px;
  margin-bottom: 12px;
  display: flex;
}

.about-name {
  color: var(--primary-color);
  font-size: 24px;
  font-weight: 400;
  line-height: 28px;
}

.about-view {
  color: var(--primary-color);
  align-items: center;
  gap: 12px;
  font-size: 14px;
  font-weight: 500;
  line-height: 20px;
  display: flex;
}

.about-desc {
  color: var(--gray-3);
  font-size: 18px;
  font-weight: 400;
  line-height: 27px;
}

.special {
  padding: 40px 0;
}

.special-center {
  width: 100%;
  max-width: 770px;
  margin: 0 auto;
}

.text-bottom {
  color: var(--gray-3);
  font-size: 24px;
  font-weight: 400;
  line-height: 36px;
  font-family: var(--font-primary);
  margin-top: 12px;
}

.special-list {
  grid-gap: 30px;
  grid-template-columns: repeat(3, 2fr);
  margin-top: 40px;
  display: grid;
}

.special-item {
  background-color: #fff;
  border-radius: 15px;
  padding: 24px 28px;
  box-shadow: 0 10px 27px #3535350d;
}

.special-image {
  cursor: pointer;
  text-align: center;
  width: 100%;
  height: 238px;
  margin-bottom: 24px;
  position: relative;
  overflow: hidden;
}

.special-image img {
  height: 100%;
}

.special-image .fa-heart, .special-image .fa-eye {
  background-color: #eee;
  border-radius: 50%;
  width: 50px;
  height: 50px;
  font-size: 20px;
  line-height: 50px;
  transition: all .2s linear;
  position: absolute;
  top: 15px;
}

.special-image .fa-heart:hover, .special-image .fa-eye:hover {
  background-color: var(--primary-color);
  color: #fff;
}

.special-image .fa-heart {
  right: -150px;
}

.special-image .fa-eye {
  left: -150px;
}

.special-image:hover .fa-heart {
  right: 15px;
}

.special-image:hover .fa-eye {
  left: 15px;
}

.special-image img {
  width: 100%;
}

.special-title {
  color: var(--black-2);
  font-size: 32px;
  font-weight: 400;
  line-height: 37px;
  font-family: var(--font-secondary);
  cursor: pointer;
  margin-bottom: 12px;
}

.special-title:hover {
  color: var(--primary-color);
  transition: all .2s linear;
}

.special-rating {
  align-items: center;
  gap: 12px;
  margin-bottom: 12px;
  display: flex;
}

.special-icon i {
  color: var(--primary-color);
}

.special-views {
  color: var(--gray-3);
  font-size: 16px;
  font-weight: 400;
  line-height: 24px;
}

.special-bottom {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.special-price {
  color: var(--primary-color);
  font-size: 40px;
  font-weight: 400;
  line-height: 40px;
  font-family: var(--font-secondary);
  text-transform: capitalize;
}

.special-arrows {
  justify-content: center;
  align-items: center;
  margin-top: 48px;
  display: flex;
}

.special-main {
  align-items: center;
  gap: 8px;
  display: flex;
}

.special-btn-arrow i {
  border: 2px solid #e6e8ec;
  border-radius: 40px;
  justify-content: center;
  align-items: center;
  width: 40px;
  height: 40px;
  transition: all .2s ease-in-out;
  display: inline-flex;
}

.special-btn-arrow i:hover {
  color: #fff;
  background-color: var(--primary-color);
}

.form-main {
  justify-content: space-between;
  align-items: center;
  gap: 105px;
  margin-top: 40px;
  display: flex;
}

.form-content {
  width: 100%;
  max-width: 544px;
}

.form-title {
  color: var(--black-2);
  margin-bottom: 4px;
  font-size: 24px;
  font-weight: 400;
  line-height: 36px;
}

.form-text {
  margin-bottom: 24px;
  font-size: 16px;
  font-weight: 400;
  line-height: 24px;
}

.form-list {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 8px;
  font-size: 16px;
  font-weight: 400;
  line-height: 24px;
  display: flex;
}

.form-list span {
  color: var(--primary-color);
}

.form-meal__select {
  width: 100%;
}

.form-meal__select select {
  color: var(--gray);
  border-radius: 12px;
  width: 100%;
  padding: 10px 20px;
}

.form-meal__add {
  color: var(--primary-color);
  align-items: center;
  gap: 4px;
  margin-top: 8px;
  font-size: 14px;
  line-height: 21px;
  display: flex;
}

.form-container {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.form-field {
  margin-top: 16px;
  font-size: 16px;
  font-weight: 400;
  line-height: 24px;
}

.form-field__title {
  color: var(--gray-3);
  font-size: 16px;
  font-weight: 400;
  line-height: 24px;
}

.form-item {
  width: 100%;
  max-width: 256px;
  position: relative;
}

.form-item.form-items {
  width: 100%;
  max-width: 544px;
}

.form-input__cash {
  width: 100%;
}

.form-item__icon {
  position: absolute;
  top: 23px;
  right: 20px;
}

.form-field input {
  border: 1px solid var(--gray);
  border-radius: 12px;
  width: 100%;
  margin-top: 8px;
  padding: 10px 20px;
}

.form-field input::placeholder {
  color: var(--gray);
  font-size: 16px;
  font-weight: 400;
}

.form-field.form-item {
  width: 100%;
  max-width: 256px;
}

.form-field textarea {
  border: 1px solid var(--gray);
  color: var(--gray);
  border-radius: 12px;
  width: 100%;
  padding: 10px 20px;
  font-weight: 400;
}

.form-price {
  color: var(--gray);
  align-items: center;
  gap: 6px;
  margin-bottom: 32px;
  font-size: 16px;
  font-weight: 400;
  line-height: 24px;
  display: flex;
}

.form-price span {
  font-size: 24px;
  line-height: 27px;
  font-family: var(--font-secondary);
  color: var(--primary-color);
  font-weight: 400;
}

.form-image, .form-image img {
  width: 100%;
}

.loader-container {
  z-index: 10000;
  background-color: #fff;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

.loader-container.fade-out {
  top: -120%;
}

.blog {
  padding: 40px 0;
}

.blog-list {
  grid-gap: 30px;
  grid-template-columns: repeat(3, 1fr);
  margin-top: 40px;
  display: grid;
}

.blog-image {
  cursor: pointer;
  width: 100%;
  transition: all .2s linear;
  overflow: hidden;
}

.blog-image img {
  width: 100%;
  transition: all .3s ease-in-out;
}

.blog-image:hover img {
  transform: scale(1.1);
}

.blog-content {
  background-color: #fff;
  border: 1px solid #e5e5e5;
  padding: 15px 20px;
}

.blog-title {
  cursor: pointer;
  margin-bottom: 8px;
  font-size: 20px;
  line-height: 32px;
  transition: all .2s linear;
}

.blog-title:hover {
  color: var(--primary-color);
}

.blog-desc {
  color: var(--gray);
  margin-bottom: 12px;
  font-size: 16px;
  font-weight: 400;
  line-height: 24px;
}

.menu {
  padding: 80px 0;
}

.menu-center {
  margin-bottom: 32px;
}

.tab {
  grid-gap: 36px;
  background-color: #fff;
  border-radius: 8px;
  grid-template-columns: repeat(3, 1fr);
  width: 100%;
  max-width: 672px;
  margin: 0 auto;
  padding: 8px;
  display: grid;
  box-shadow: 0 3px 9px #0105140d;
}

.tab-item {
  cursor: pointer;
  justify-content: center;
  align-items: center;
  gap: 8px;
  padding: 8px;
  transition: all .2s linear;
  display: flex;
}

.tab-item img {
  width: 45px;
  height: 40px;
}

.tab-item.active, .tab-item:hover {
  background-color: var(--primary-color);
  color: #fff;
  border-radius: 8px;
}

#fast-food, #thailand-food {
  display: none;
}

#korean-food #fast-food, #thailand-food {
  -webkit-animation: 2s ease-in slide-down;
  -moz-animation: 2s ease-in slide-down;
}

.contact {
  padding: 80px 0;
}

.contact-main {
  justify-content: space-between;
  align-items: center;
  gap: 135px;
  display: flex;
}

.contact-content {
  width: 100%;
  max-width: 524px;
}

.contact-title {
  text-transform: capitalize;
  color: var(--black-2);
  font-size: 48px;
  font-weight: 400;
  line-height: 55px;
  font-family: var(--font-secondary);
  margin-bottom: 12px;
}

.contact-text {
  color: var(--gray);
  font-size: 22px;
  font-weight: 400;
  line-height: 32px;
}

.contact-text span {
  color: var(--primary-color);
}

.contact-btn {
  width: 100%;
  margin-top: 20px;
}

.contact-button, .contact-map {
  width: 100%;
}

.our-blog {
  padding: 80px 0;
}

.our-blog__heading {
  color: var(--black-2);
  text-transform: capitalize;
  margin-bottom: 32px;
  font-size: 24px;
  font-weight: 500;
  line-height: 36px;
}

.our-blog__main {
  justify-content: space-between;
  gap: 17px;
  margin-top: 32px;
  display: flex;
}

.our-blog__post {
  width: 100%;
  max-width: 636px;
}

.our-blog__image {
  width: 100%;
}

.our-blog__image img {
  width: 100%;
  height: auto;
}

.our-blog__content {
  margin-top: 20px;
}

.our-blog__date {
  color: var(--gray);
  margin-bottom: 12px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.our-blog__title {
  color: var(--gray-3);
  margin-bottom: 12px;
  font-size: 24px;
  font-weight: 500;
  line-height: 36px;
}

.our-blog__text {
  color: var(--gray-3);
  margin-bottom: 32px;
  font-size: 14px;
  font-weight: 400;
  line-height: 21px;
}

.our-blog__tag {
  align-items: center;
  gap: 16px;
