.our-blog__post-2 {
  flex-direction: column;
  gap: 17px;
  width: 100%;
  display: flex;
}

.our-blog__items {
  gap: 17px;
  display: flex;
}

.our-blog__list__image {
  width: 100%;
  max-width: 313px;
}

.our-blog__list__content {
  width: 100%;
  margin-top: 0;
}

.our-blog__images {
  height: 243px;
}

.all-blog {
  padding: 80px 0;
}

.all-blog__list {
  justify-content: space-between;
  gap: 175px;
  display: flex;
}

.all-blog__left {
  flex-direction: column;
  gap: 40px;
  width: 100%;
  max-width: 637px;
  display: flex;
}

.all-blog__right {
  width: 100%;
}

.our-blog__title-2 {
  font-size: 20px;
  line-height: 30px;
}

.our-blog__title-3 {
  margin-bottom: 8px;
  font-size: 16px;
  font-weight: 500;
  line-height: 24px;
}

.all-blog__box {
  border: 1px solid #e6e8ec;
  border-radius: 8px;
  flex-direction: column;
  gap: 24px;
