  display: flex;
}

.product-detail {
  padding: 80px 0;
}

.product-detail__main {
  justify-content: space-between;
  align-items: center;
  gap: 86px;
  display: flex;
}

.product-detail__image {
  border: 1px solid #ccc;
  width: 100%;
  max-width: 420px;
}

.product-detail__image img, .product-detail__content {
  width: 100%;
}

.product-detail__heading {
  color: var(--gray-3);
  font-size: 16px;
  font-weight: 400;
  line-height: 24px;
}

.product-detail__title {
  font-size: 48px;
  font-weight: 400;
  line-height: 55px;
  font-family: var(--font-secondary);
  text-transform: capitalize;
  color: var(--black-2);
}

.product-detail__price {
  font-family: var(--font-secondary);
  color: var(--primary-color);
  margin-bottom: 12px;
  font-size: 40px;
  font-weight: 400;
  line-height: 46px;
}

.product-detail__review {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 12px;
  display: flex;
}

.product-detail__list {
  align-items: center;
  gap: 12px;
  display: flex;
}

.product-detail__rating {
  color: var(--primary-color);
}

.product-detail__view {
  color: var(--gray-3);
  font-size: 16px;
  font-weight: 600;
  line-height: 24px;
}

.product-detail__viewall {
  color: var(--gray);
  font-size: 16px;
  font-weight: 400;
  line-height: 24px;
